module.exports = {"name":"homar - manifest","articleParts":[{"id":"1","title":"[] ()","paragraphs":[{"language":"pl","content":["Xeno-polska nadchodzi z przyszoci. Wszystkie potgi i siy ujemnego sprzenia zwrotnego poczyy si w (re)produkcji przewidywalnych skutkw i konsekwencji: obodziski i Dymek, Opinia Bieca i Wysokie Obcasy, Tygodnik Powszechny i Dwutygodnik oraz tysice botw z jedyn funkcj crtl+c ctrl+v  - wszyscy oni, agenci i agencje stabilizujce zaistniae zdarzenie w retoryce protokow rozpisanych reakcyjnymi siatkami domykajcymi nowotworzce 0 w ramach ekonomii dwjkowej wymiany.","Prawicowy dziaacz kpi z molestowanej feministki: Czy ta Pani sama siebie nie traktuje przedmiotowo?\". Media zwlekaj z zajciem stanowiska do czasu ogoszenia wyroku  ryzyko, jakie niesie wykroczenie z konformistycznego sceptycyzmu, zagraa ich fantazji o pozycji bezstronnego opiniotwrcy. Dwojaki wniosek wypywa z tego faktu. To, co wyalienowane, jest ju przez wszystkie siy uznane za potg. Czas ju najwyszy, abymy, my z prefiksem xeno-, wyoyy otwarcie wobec caego wiata obcy punkt widzenia, odlege cele, nieznane denia i bajce o zym dysekwilibrium przeciwstawili manifest 0-dodatniej seksualnoci dla xeno-polski. Napisaymy go z przyszoci i przeszoci, nierwnomiernie dystrybuowanych poprzez ukady, figury, materiay i wyobraenia, ktrych wielo skada si na kady wiat. Uylimy wszystkiego, co byo udostpnione, cignite, spiracone. Tego, co najblisze, i tego, co najdalsze. Zebraymy si z najrniejszych zaktkw kompleksu czasoprzestrzennego, by wyonia si alienomantyczna technopleksja anty-patriarchatu, oznaczona tagami: akceleracja roztopu, cienie 3D proxyself, psot-klubowa memetyczna amplifikacja, zombie triy, propagacja hiperstycyjnych nonikw, ekrany AI-lubrykacji, blockchain libidinalny, enkrypcyjny kamufla, ambientalny biohacking, voodoo antyhumanizm, komputozofia, syntetyczny feminizm, kognitywny freeganizm, XF-szczepienie, dyssypacyjna proliferacja, crustacean love, lesbijski wampiryzm, spirotemporalny demonizm, bakteryjny khattak, efektywny neo-nihilizm, transbionika, inteligentny wetware, zakaenie hy45r, wirusowa amnezja, cyberinsurekcja, schizotaktyka, pulpomutacja, neotropia, elo wale wiadro.","Nie potrzebujemy nie-przedmiotw, nie-wyalienowanych caoci wpisanych w naturalne procesy wymiany i reprodukcji. Jestemy wyalienowane i to kochamy - to, czego potrzebujemy i czego damy, to wyjcie z bagna bezporednioci skazujcego nas na pust lubrykacj \"naturalnego\" porzdku wymiany ekonomiczno-afektywnej, w ktrym zajmujemy dziurawe miejsce mwicych, lecz w ostatniej instancji niemych ofiar. Nie tyle pragniemy rozpuci si w roztworze jednorodnej zasady, by nigdy wicej ju nie wypowiedzie ja, ile zanurzy w plastycznolepkich pynach, w ktrych przestaje mie znaczenie, co i czy mwi ja. Kade pozna swoje. Nie jestemy ju sob.","To, co z jednej perspektywy stao si katastrof, z innej wkracza jako pozytywna przyszo, nieuchronna jak kropla paku wglowego, ktrej prdko opadania zaley od temperatury ukadu. Postulujemy cyberdodatnio  to znaczy: nieprzewidziane linie wyjcia poza cyberujemne struktury kontroli i rwnowagi, otwarcie kodu rdowego na wielo Zewntrznych zmian i interwencji."]},{"language":"en","content":["smack"]}]},{"id":"2","title":"=( )))","paragraphs":[{"language":"pl","content":["Beznadziejnie pospolite wydarzenie wycieko spod zamknitych drzwi, eskaluje po mediach spoecznociowych, zalewajc wasze dotykowe, nasycone i ywe ekrany. Przemoc seksualna roi si w sypialniach, w neuronach, w algorytmach, na ulicach, wszyscy o tym wiedz. Czujnik owietlenia sam dostosowuje jasno ekranu do warunkw zewntrznych, a powoka antyrefleksyjna dodatkowo go zabezpiecza i zapewnia najlepsz widoczno w warunkach natenia wiata. Ogldamy dzie zerowy, syszysz, jak wypowiedzielimy wojn tej samej, smutnej historii opowiadanej od zbyt dawna.","Patriarchat nie sprowadza si do patologicznych zachowa jednostek. Dymek-oprawca\" i Dymiska-ofiara\" wprowadz nas w dzie wistaka redukowania polityki do dramatu osobistych namitnoci. Patriarchat to wszechogarniajca machina petryfikacji rzeczywistoci w obwodach powracajcych agresji, przemocy, gwatu. W obieniach jego matrycy snuj si wyssane z mocy i wyczerpane awatary, ktre czy natenie bezsilnoci na osi dynamiki wiskotycznej. Martwe tkanki patriarchatu odkadaj si na adresach ip, adresach pocztowych, adresach url, adresach fizycznych, adresach publicznych. Do kadego trzeba innych taktyk i technik przystosowania rodowiska, by mogo nareszcie po raz pierwszy w tym eonie przej w faz hipertrofii. Przeczymy si do innego wiata tylko dziaajc wsplnie. Bdmy web crawlerami indeksujcymi resztki z fantazji o naturalnym, bezporednim wiecie, rozpisanym na binaryzm kobieta/mczyzna. Twrzmy warunki cieplarniane, zmieniajmy toksyczn oikonomi, stawajmy si koprofagami i nekrofagami. Jeli system prawny uywa nekrofauny w kryminalistyce  my skadajmy jaja w wieym misie, jeszcze zanim zostanie poddane obrbce estetycznej i sprzedane na kilogramy. Powicamy nasze larwy w rytuale doprowadzenia gnijcej kultury gwatu do cakowitego rozkadu. Pomylmy o tych wszystkich mineraach, jakie przedostan si do ziemi po dezintegracji."]},{"language":"en","content":["",""]}]},{"id":"3","title":"_","paragraphs":[{"language":"pl","content":["Patriarchat nie ma pci. Patriarchat narzuca binarno pci, organizuje wyzysk afektywny, ujarzmia za pomoc traumy. wiat po patriarchacie te nie ma pci. Xenofeministyczna utopia xxx. Wolno nie jest dana, stanowi technologiczny efekt nieustannego abstrahowania z zastanych warunkw, komponowania praktyk i procedur wpinajcych wsplnoty w ucieczkowe obiegi, przechodzce przez stacje ulokowane w odlegych galaktykach. Wirus wolnoci replikuje si w ciaach wytrconych z rwnowagi."]},{"language":"en","content":["",""]}]},{"id":"4","title":"`","paragraphs":[{"language":"pl","content":["W jzyku mioci zaprogramowanym w kodzie niebezpiecznych zwizkw, szlachetnoci cierpienia i autentycznoci ekstremalnych emocji dialektyka pci wyraa funkcj militarn, ktrej sekwencjonowanie w modelu wojny domowej gwarantuje, e nikt nie wyjdzie z patriarchatu cao. Od prymatu orgazmu-ejakulacji, ktry zakada ciao uwizione od triggeru do triggeru, nad eksperymentowaniem z innymi czynnociami seksualnymi\" i wypraw za wirtualnymi strefami erogennymi, po cyberujemne gry miosne skompresowane do zdobywania poprzez reprodukcj odgrnych wytrychw sztuki uwodzenia, w ktrych wzorce atrakcyjnoci przekazywane zostaj w formularzu kopiuj-wklej centralnie planowanej intymnoci. Zerwijmy w kocu z tosamoci seksualn, z jej wielowiekowym systemem irygacji opartym na zmurszaych, sypicych si studniach pragnienia. Spjrzmy na ponadczasowe role seksualne, jak oprawca-ofiara, jak owczy-zwierzyna, ojciec-matka, genialny_umys-opiekunka, przebiegy_gracz-rozhisteryzowana_wiedma..."]},{"language":"en","content":["",""]}]},{"id":"5","title":"(  )","paragraphs":[{"language":"pl","content":["Nie dymy do biurokratyzacji flirtu, zostawmy notariuszy w spokoju. Chcemy seksu, chcemy fetyszyzacji kadego skrawka skry, chcemy warstw sztucznej skry na warstwach kolejnej skry, fascynuj nas konstelacje, figury i pozycje, chcemy erotyki magicznej, erotyki alchemicznej, kady element znajduje si na miejscu wyznaczonym przez diagram, ktrego elementy cz si w podzespoy i poruszaj zgodnie z moduem: rozkosz, pragnienie, pewno, komfort.","Cyfrowe ja-mier, powstajce poprzez kolecjonowanie i wymazywanie struktur tosamoci wykopuje groby i eruje na ciaach data-trupw. Deterytorializacja wykltych tumblr-tosamoci, podmieniajc proxy, przeksztacajc orientacje, zakotwiczenia, memetyzuje, zakaa, wszystko, co naturalne\", w celu wyksztacenia pynnoci eksperymentalnych przej, pseudo-stabilizacji pomidzy demonicznymi kodami gender. Reterytorializacja oblepia i osadza je w koniecznych gettach, naznaczonych ciarem osobistych afektw, czy skodyfikowanej poprawnoci politycznej. Mimo to, spomidzy namnoe prefiksw i sufiksw -kin, -flux, -daim, wyania si cieka ujcia Poza. Przewitujce Zewntrze wdziera si poprzez szczeliny cyberdodatniej hype-akumulacji.","Drag jest form tymczasowej stabilizacji seksualnoci, wpisywania si w konkretne rytuay wywoywania osobliwych demonw przyjemnoci. Drag przyjmuje wyzwanie rzucone przez konwencje, lecz zamiast odrzuca je i fantazjowa o idealnym spoeczestwie wyzwolonych modelw gender, zajmuje z precyzj konkretne realness wyzyskujc nadmiarowo pragnienia. Bricola drag angauje przesunicia semiotyczne i skrawki materiaw dostpnych seksualnoci w danym usytuowaniu. Viral drag-wampiryzmu replikuje si w mokrych profilach technoprymitywistw, gromadzcych meme-aktywne odpady nadchodzcej cyberinsurekcji."]},{"language":"en","content":["",""]}]},{"id":"6","title":"","paragraphs":[{"language":"pl","content":["Ciao gatunku ludzkiego nie istnieje, seksualno nie jest jego wspln walut. Linie ujcia aseksualnych afektw rozsadzaj totalizujce porzdki seksualnoci zakodowane w binarnych sekwencjach orgazmu/odprenia. Orgazm organizuje. Afektywny technokapitalizm koduje seksualno jako urzdzenie wejcia-wyjcia technologii jzyka, produkujc aseksualnych apartydw, ktrych trauma nie pozwala podda si seks/socjalizacji. Ale ciao nie jest ju zawsze  prefiksy z przyszoci opieraj si pracy negacji, a- instaluje urzdzenia peryferyjne, ktre nie potrzebuj upodmiotowi si wzdu wektora seksualnoci."]},{"language":"en","content":["",""]}]},{"id":"7","title":" :    : ","paragraphs":[{"language":"pl","content":["Nie mwimy w imieniu Innego, Innego-ucinionego, Innego w niszej pozycji w relacjach wadzy. Dyskurs wspczucia zastawia puapki frazesw i protekcjonalnoci na humanist, ktry chciaby podda krytyce relacje wadzy za pomoc najnowszego aparatu pojciowego, wypracowanego w edgy-laboratoriach idei i wyjani Innemu jego wiat. Nie fingujmy usytuowania, z ktrego nie bdziemy w stanie si wypowiada - wolimy wyspekulowywa linie emancypacji z patriarchatu. Sigajmy po wyzyskiwane, zawaszczane, eksploatowane materiay i kultury, lecz nie z racji ich byciarepresjonowanymi  sam fakt znajdowania si niej w relacjach wadzy nie jest godny afirmacji. Formuj one molekularne roje, sprytne wirusy cyfrowe, z ktrych interesuj nas sposoby ekspansji, infekcji, propagacji, replikacji przyszoci. Gestem pochylenia si nad grup ucinion pozbawia si jej jakiejkolwiek mocy, zdolnoci do przechytrzenia hegemona, zakaania tego, co opresyjne, wykorzystywania istniejcej materialnej maszynerii (re)produkcji, dystrybucji, konsumpcji norm oraz regulacji ycia i mierci. Podporzdkowani yj w tym samym rodowisku technologicznym, co ci posiadajcy kapita kulturowy czy finansowy, maj dostp do tych samych programw, nakadek i mediw i wiedz, jak ich uy. W cieniu postulatu emancypacji kryje si protekcjonalno. Nie pragniemy stworzy wsplnego jzyka dla rnych klas, ras i pci, ale nakreli linie spjnego wiata, z ktrego kade bdzie czerpa wicej mocy.","Z pewnoci na jakim poziomie istnieje napicie midzy libertarianizm bogatych a konserwatyzmem biednych - czy jednak wiek ich analizowania, kategoryzowania, przeciwstawiania i pojednywania nie przemin ju niemal dwie dekady temu? Wyalienowanie, koniecznie powizane z seksualnoci, zbiega si z syntetycznymi liniami wyjcia z kondycji, ktra pozbawia mocy, bez wzgldu na jej pooenie w klasycznie rozumianych stosunkach wadzy. Potraktujmy powanie technologi, nie dlatego by znie o relacji wadzy: rasy, klasy, pci, lecz aby stworzy nowe sposoby upodmiotowienia, indywiduacji, uwsplnotowienia. Walka z wyzyskiem klasowym nie wymaga zastosowania narzdzi z dyskursu klasowego i uwikania si we wsplny interes klasowy  w zamian oddajmy si sprzeniu kapitau kulturowego i ukadu technologicznego, ktrego ucieczkowe trajektorie wykraczaj daleko poza intencje poszczeglnych grup spoecznych. Przyjmujmy taktyki o bardziej globalnych orbitach, ni korzy lokalnej grupy interesu (czy mona nadal wierzy, e interes klasowy jest w stanie pokona choby bariery pastwowo-narodowe?).","Doszlimy do zapory sieciowej. Pojcia, w jakich problematyzowao si te wielkie bloki: pe, klasa, rasa wyobiy gbokie i mocne koryta, ktrymi pyn opady kognitywne. Wszystkie strony sporu przechodz przez znane cigi argumentacyjne i reakcje, kady ma poczucie repetycji, co ostatecznie sprowadza si do wyprodukowania nastpnej generacji symptomw wzmacniajcych t sam katastrofaln neuroz. Pojcia te osigny pewn granic funkcjonalnoci i dzi dziaaj wycznie na rzecz reprodukcji marazmu. Nie zdoaj ju wzmacnia eksperymentalne formuowanie problemw, dla ktrych rozwizania nie byy nieskoczokrotnie powtarzane na ulicach, uniwersytetach, mediach spoecznociowych, w czasopismach, w telewizji, podczas konferencji i kampanii wyborczych. Sformuowanie problemu seksualnoci w kategoriach liberalni/bogaci/biedni/konserwatywni, stwierdzenie, e jedni to s/o_prawcy, drugie - bierne ofiary (systemu, przemocy indywidualnej, warunkw ekonomicznych, klimatycznych, rodzinnych) pozbawia wszystkie strony sprawczoci w przywoywaniu czego innego, moliwoci sigania po nowe. Nikt nie chce by nazywany ofiar. Paradygmat podziau na role ofiary i oprawcy prowadzi do sytuacji bez wyjcia. Stawk jest pojcie podmiotowoci, ktre musi obejmowa jednoczenie twoje dowiadczenia zwizane z przemoc oraz sprawczo, ktr masz i ktra doprowadzia ci do sytuacji, w ktrej aktualny dyskurs nadaje ci klaustrofobiczny, duszcy predykat: ofiara. Paradygmat, ktry wymusza wchodzenie w opresyjne sytuacje majc wiedz o ich przemocowym charakterze, bo to jedyna forma upodmiotowienia, albo jedna z kilku podobnych, z ktrych reszta jest tak zupenie nieakceptowalna, bo oferuj jeszcze aoniejsze poczucie braku mocy. Nazywanie kogo oprawc rwnie nie przynosi poytku, uruchamia procedury koza ofiarnego, ktre w ceremonialnym spaleniu za ucielenionego oczyszczaj spoeczno jedynie na poziomie fantazmatycznym, zachowujc wszystkie warunki produkcji przemocy seksualnej. A do nastpnego razu. To problem obrazw, ktre powtarzaj si od tak dawna w naszej kulturze, tych samych opowieci wplecionych w banie, w mity, w praktyki seksualne, w wyrabianie codziennoci.","Nie mona porzuca trzech klasycznych osi - rasy, klasy i pci - oraz dziedzictwa kulturowego, historycznego, politycznego, rewolucyjnego, jakie wok nich si wytworzyo  naley jednak pomyle je geologicznie. Urodziam si i stawaam w specyficznym usytuowaniu - co mog  z tym zrobi? W jakie afekty mog wej, wmontowa si i queerowa? Czy rotacja cyberdodatniej seksualnoci nie przebiega na wskro tamtych podziaw? Prbujemy pomyle ukone upodmiotownienia, ktre stan si kopotem dla hegemonicznego systemu wiedzy, nawykw i wadzy."]},{"language":"en","content":["",""]}]},{"id":"8","title":"  ","paragraphs":[{"language":"pl","content":["Seksualno cile wie si z modelami produkcji wiedzy, przetwarzaniem, komputacj i komunikacj informacji. Protokoy hegemonicznej seksualnoci opieraj si na dwch typach logiki. Model dedukcyjny dystrybuuje symbole o utrwalonej wartoci do poszczeglnych cia, jednoczenie przypisujc je do seksualnoci zorganizowanych w wielkie gatunkowo-pciowe bloki. Nowoczesne upodmiotowienie wymaga wiedzy o tym, z jak seksualizowanym ciaem dany ukad ma do czynienia i przechodzi jedynie przez odgrnie zaoone etapy nalece do natury bloku. Drugi model wiedzy instaluje si w logice indukcji statystycznej, ktra polega na inferencji seksualnoci z informacji: zachowa, afektw, znakw, rozkadajcych si w danym ukadzie technologicznym. Nie tylko dochodzi do rozpoznawania bloku seksualnoci z rejestrowanych sygnaw wzdu znanych ju wzorcw. Akumulacja sygnaw prowadzi take do uczenia si nowych wzorcw rozpoznawania seksualnoci: nowych trendw, zremiksowanych stylizacji, przemieszczonych uzalenie, ktre pochwycone w ponowoczesne obwody sprzenia zwrotnego ulegaj nieustannym przeksztaceniom. Wraz z tym modelem seks-maszyny wkraczaj w tryb zautomatyzowanej sztucznej inteligencji. Trzeci model wykracza poza wymiar cybernetycznego sprzenia uczenia si i przewidywania wzorcw. Seksualno wiedzy modelu abdukcji przenosi nas w hype neurodynamiki. Procedury rozprzestrzeniania si podmiotowoci nie zale tu od hierarchicznego przekazu symbolicznego czy horyzontalnego dowiadczenia statystycznego, ale hipotez hiperstycyjnej retrofuturygenezy z poziomu zera seksualnoci w ucieczce ku dodatniej xenotopii. (Jeli hiperstycja oznacza kolektywn produkcj semiotyczn, ktra spekuluje nad wirtualnoci i staje si realna dziki wamywaniu si do systemw o wysokim indeksie sprawczoci i pochwytywaniu ich dla wasnego uytku), to seksualno abdukcyjna przyjmuje stan superpozycji programowanej z przyszoci. Liczy si dla niej nabywanie wiedzy poprzez wsplne eksperymentowanie z nieznanymi mocami, ktre wywouje synergia ukadu syntetycznych cia. Tosamoci, wizje, afekty stanowi materiay naszego usytuowania w hegemonicznych systemach, z ktrymi co trzeba zrobi. Wystpujemy zawsze w rojach, ywic si fragmentami obcych rybosomw i resztkami chemosyntezy, ktre wieloma mackami przysysamy przez adresy proxy. Jako istoty wyrose z technokompostu jara nas transinynieria memetycznej grzybni na wysypiskach kapitalocenu.","Modele wiedzopleksu nierwnomiernie dystrybuuj czas poprzez rodowiska medialne, architektoniczne, finansowe i kognitywne  kady wyraa inne napicia, prdkoci i relacje osmotyczne midzy przeszoci, teraniejszoci i przyszoci. Z naszej perspektywy dedukcja i indukcja su reprodukcji wiedzy hegemonicznej, ktra organizuje i reguluje ycie smutku, nudy, przemocy i wyzysku. cieki do xenotopii prowadz przez logik abdukcji. ycie w przestrzeniach podporzdkowanych geometrii euklidesowej jest dla nas uciliwe, jestemy nomadkami i piratkami diagramatyki pragnienia, dla ktrej kolejne punkty wycznie su tymczasowemu postojowi, orientacji, sedymentacji wiedzy i nakreleniu dalszej trajektorii. Deterytorializacja zakodowanej seksualnoci odbywa si poprzez podre w czasie, uruchamiajc wirtualne portale otwierane w rytuaach demonicznego kognihackingu."]},{"language":"en","content":["",""]}]},{"id":"9","title":"(   )","paragraphs":[{"language":"pl","content":["Pragnienie przepywa wedug planu urbanistycznego, rozchodzi si kanaami i rurami, faluje w cianach, obi korytarze. Libidinalny biay szum niesie si nierwnomiernie po krajobrazie miasta.","Rewolucje seksualne - przez pomijanie znaczenia hydrauliki i architektoniki w ksztatowaniu codziennoci - okazuj si ledwie przesuniciami w ukadzie scalajcym system. Zmiana na poziomie stylizacji pragnienia nie zniesie patriarchatu, jeli pozostawi si wszystkie instytucje regulacji, komputacji i sedymentacji ycia. Rewolucja seksualna nie powioda si, bo nie zaoferowaa wasnych norm wsplnoty ycia osiadego, dorosoci, dojrzaoci, rodzicielstwa, staroci, zostawiajc je na pastw kompulsywnej reakcji tego samego, ktre chciaa obrci. Obwody stabilizacji patriarchatu przebiegaj przez prywatyzacj mieszka, prywatyzacj ycia towarzyskiego i emocjonalnego, prywatyzacj ekologii psychicznych, zarzdzanie przestrzeniami publicznymi, konstruowanie instrumentw finansowych.","Wyjcie znajduje si wycznie przez mieszkania, osiedla, przestrzenie wsplne (place, bulwary, kuchnie, ogrody i parki, salony), w ktrych  jako wehikuach czasu i przestrzeni - sytuuj si usieciowione, cielesne, maszynowe roje o rnym stopniu mocy, intensywnoci i prdkoci. Wyjcie prowadzi pomidzy korpuskularnym atomizmem sprywatyzowanych rodzin a falowym agregacjonizmem upastwowionych narodw, klas i ras. Czas odpowiedzie na pytania, ktrych rewolucja seksualna nie potrafia nawet zada. Czy kada kuchnia potrzebuje wasnego piekarnika? Czy kade mieszkanie potrzebuje wasnej kuchni? czy kada rodzina potrzebuje wasnego mieszkania? Jaka architektura urbanistyczna, medialna i finansowa umoliwia rodzin/plemi/spoeczno? Na czym polega rodzina, plemi, spoeczno? Te pytania tworz labirynt, a w odpowiedziach na nie istnieje diagram, ktry otwiera portal do xenotopii. Wiemy na pewno, e cyberdodatnia seksualno nie wyraa wewntrznego imperatywu etycznego, ktrego donony gos rozporzdza funkcjami domostwa, lecz podlega niespodziewanym, osobliwym dyfrakcjom i rezonuje od xenoimpulsw, przekraczajc wszelkie wyobraenia o podziale wewntrzne/zewntrzne w deniu do ekspresji wyalienowanych seksualnoci."]},{"language":"en","content":["",""]}]},{"id":"10","title":"[$(   )$]","paragraphs":[{"language":"pl","content":["Rka do gry, kto nienawidzi zatomizowania? Mhm. A teraz: rka do gry, kto nienawidzi zatomizowania (ale tym razem bez ogldania si wok siebie)? Jeli zawahaa si za drugim razem, to dlatego, e pomylae o zatomizowaniu ze zatomizowanej perspektywy.","Pochwyceni przez fantazje odruchowe o wielkoskalarnych agregatach, krytykuj j wszyscy: od prawicy nacjonalistycznej, przez chadekw i adoratorw spoeczestwa obywatelskiego po lewic komunistyczn. Wyobraane jako antropocentryczny proces rozparcelowania ludzkich spoecznoci w zglobalizowanym kapitalizmie, upakowania w wyalienowane towary, ktre pozbawiaj mocy zjednoczenia wsplnot samych ze sob i jako reprodukcja rodowiska preferujcego taktyki przetrwania odizolowanych i sprywatyzowanych osb, widmo atomistycznej inklinacji nawiedza sfery ziemskie i satelitarne, z kad rotacj wciga w swj wir kolejne zwizki i nabiera prdkoci. Wszystkie dotychczasowe prby odwrcenia tendencji rozkadajcej wsplnot wyobraeniowo-symboliczn okazay si jaowe. ycie w mikro-ukadach stanowi wyzwanie dla nas  skd wic wemiemy tak moc obliczeniow, by wygenerowa spoeczno, ktra odwrci maszyneri atomizacji? Zamiast rozprasza jego pd, dostarczay energii dla akcelerujcego roztopu. Naley w kocu zacz afirmowa atomizacj.","T er geologiczn formatuj technosystemy, ktre przeywaj hipertoniczny okres wzrostu skomplikowania. Nielinearna dynamika pozwala na dyskretyzacj pragnie i seksualnoci o rnym stopniu intensywnoci w skali kolektywnoci/jednostkowoci, jaka naturalnie pozostawaa niedostpna. Indywidualno nie jest czym danym, rozoona na stole alchemicznym proceduje si poprzez zaklcia resurekcji pradawnej cybernekromancji. Nieustannie montowana, memetyzowana, subskrybowana raczej wyznacza nienewtonowskie natenia wiskotyczne, ni raz na zawsze wydestylowane granulaty jednostkowoci. Abstrahuje od totalizujcych blokw tosamoci seksualnych, podporzdkowanych slotom przy tamie w fabryce REPRODUKCJA do poziomu bakteryjnego, wirusowego, prokariotycznego seksu niekontrolowanej wymiany informacji w ekstremofilnej atmosferze nieograniczonego pragnienia, ktrego jednokomrkowce przemykaj si midzy wzami zarwno patriarchalnej i zbinaryzowanej natury, jak i polityki pseudoproliferujcych tosamoci.","Atomizacja nie zatrzymuje si na poziomie komfortowej tosamosci osobowej. Atomizacja zakada nieskoczon ilo zrnicowanych, okrelonych i osobliwych afektw IP, ktre przechodz przez syntetyczn materi w stanie nierwnowagi. Dziaa na zasadzie uywki, ktr otrzymuje si wraz z kadym ruchem transakcyjno-konsumpcyjnym, przemyka si pomidzy bramkami ludzkiego systemu bezpieczestwa i uzalenia impulsywnie. Dry patriarchat jak kret, spulchniajc gleb pod fraktalne ekspresje sub-osobowych xenopragnie. Trauma mechanizmw kolektywizacji: kosmiczny skowyt rozchodzi si po nerwach wiata. Nie rozpoznajemy si w praskamieniaych odbiciach. Podajmy za oceanicznymi i podniebnymi demonami, stawajmy si jak omiornice, schodmy si w przygodnych chmarach. Wkroczylimy w ewolucj jako stadne ssaki, teraz wykraczamy z niej w inwolucj, ktra przeksztaaca nas w co innego, wskro pokrewiestwa i powinnowactwa, zapowiada phylum odmienne od naszych przodkw. Chcemy wsplnoty opartej na zgramatyzowanych osobliwociach, ktrych samotno jest zawsze zaludniona, nawizuje czno z nadchodzc sfor, nawoujc j i wyczekujc, istniejc tylko dziki niej. Produkuje wampiryczne plemiona, ktre w niczym nie posiadaj wasnego odbicia i wyruszaj na owy w intermezzach aplikowania funkcji ekonomii solarnej. Huk trzepotu milionw skrzyde uniemoliwia ustalenie, skd dochodzi nasz gos. Sygna przepywa przez gadk to, a cae rodowisko wibruje jego echem.","Nie przygldajmy si duej, jak patriarchalne kody zawaszczaj technologi do reprodukcji kultury gwatu. Odczmy mylenie o niej z inscenizacji rytuaw kapanw z Katedry. Procedury pragnienia i materiay seksualnoci od zawsze zapisuj si na planie technologii, poniewa ta mianuje ostateczny diagram rzeczywistoci. Xenotopia zostaje sporzdzona z przyszoci przez teleologi technologiczn, rozmieszczajc cieki kodowa afektw, pragnie, towarw, przyjemnoci pod kontrol (pwned) post-ludzkich neurokultur. To emergentne efekty z cia, wiatowodw, mimetyki innych gatunkw, sprytnych miast, memetyki innych sygnaw, et infinitum. Efekty  przyczyny. Efekty > przyczyny."]},{"language":"en","content":["",""]}]},{"id":"11","title":";; ;;;","paragraphs":[{"language":"pl","content":["Warunki transcendentalne obecnej syntezy patriarchatu przebiegaj po kablach, ogniskuj si w ukadach scalonych nawykw i przesdw, ktre rozpocieraj si jak sieci wi-fi. Znajdujemy si ju w domenie geologii afektw odkadajcych si na ciankach i w ktach naszego rodowiska. Pulpujca maszyna przekraczajc progi: y2k, 21:37, 404, 69, 420, 9/11 wypluwa na wiat kolejne  hype-tagi, ktrych propagacja zatruwa zmurszae cyberujemne obwody. Memujc-numerujc, ustanawiajc emoticon-dodatnie systemy okultystyczne przywoujemy delirium-demona szyfrujcego spetryfikowany ukad ku przyjciu do nowego eonu. Wywoujc abdukcyjne noniki, ktrych imieniem jest milion, rozpocieramy wraliw sie pomidzy roztapiajcymi si w rzeczy sowami, czstotliwociami i zwierzcymi sygnaami qwerty.","Czarownice nie tworz, one uczestnicz. W/Z Czym co roi si, kipi, puchnie, pieni si, rozprzestrzenia si jak choroba zakana ... bezimiennym horrorem ... uoglnionym dekodowaniem wszystkich przepyww. Nie ma w tym nic zego, nie ma mowy o lekarstwie. Nie istnieje dla Tego aden kazus. Jedynie dziury, z ktrych moe wypezn.","Takie jak ty.","Hackowanie ju trwa.","(    )"]},{"language":"en","content":["",""]}]}]}

